.body {
  font-family: 'Open Sans', sans-serif;
  color: #000;
  font-weight: 400;
}
ul {
  padding: 0 0 0 0;
  margin: 0 0 0 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0 0 0 0;
}
p {
  margin: 0 0 0 0;
}
fieldset {
  padding: 0 0 0 0;
  border: none;
}
blockquote {
  margin: 0 0 0 0;
}
figure {
  margin: 0 0 0 0;
}
img {
  object-fit: contain;
}
li {
  list-style: none;
}
.container {
  max-width: 1136px;
  margin: 0 auto;
}
.visually__hidden {
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.button {
  cursor: pointer;
  background-color: transparent;
  border: none;
}
.header__wrapper {
  background-color: #F5F5F5;
  min-height: 40px;
}
@media (max-width: 1440px) {
  .header {
    display: none;
  }
}
.header__mobile {
  padding: 11px 16px 11px 16px;
  box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, 0.25);
  position: sticky;
  top: 0;
  background-color: #FFFFFF;
  z-index: 2;
}
@media (min-width: 1440px) {
  .header__mobile {
    display: none;
  }
}
.menu__item--active {
  background-color: #FFFFFF;
  transform: matrix(1, 0, -0.6, 1, 0, 0);
}
.menu__item--active .menu__link {
  display: flex;
  transform: matrix(1, 0, 0.6, 1, 0, 0);
  color: #000;
}
.menu__link {
  padding: 10px 10px 10px 9px;
  text-decoration: none;
  color: #959595;
  font-size: 18px;
  line-height: 23.4px;
}
.menu__list {
  display: flex;
  align-items: center;
  min-height: 40px;
}
.menu__nav {
  display: flex;
  align-items: center;
  max-width: 1440px;
  margin: 0 auto;
}
.menu__list--flex {
  flex: 1;
  justify-content: center;
}
.menu__item {
  display: block;
  padding: 0 10px 0 10px;
}
.social__list {
  display: flex;
}
.social__item {
  margin-right: 16px;
}
.social__item:last-child {
  margin-right: 22px;
}
.header-advantage__svg {
  width: 48px;
  height: 48px;
}
.header-advantage__list {
  display: flex;
  align-items: center;
  margin: 28px 0 28px 0;
  padding: 0 35px 0 38px;
}
.header-advantage__figure {
  display: flex;
  align-items: center;
}
.header-advantage {
  display: flex;
  align-items: center;
  max-width: 1440px;
  margin: 0 auto;
}
.header-advantage__figcaption {
  max-width: 224px;
  margin-left: 10px;
}
.header-advantage__item {
  margin-right: 64px;
}
.header-advantage__item:last-child {
  margin-right: 0;
}
.contacts__svg {
  width: 24px;
  height: 24px;
  fill: #D93F3C;
  margin-right: 8px;
}
.contacts__link {
  font-family: 'Libre Franklin', sans-serif;
  font-weight: 400;
  font-size: 25px;
  color: #D93F3C;
  text-decoration: none;
}
@media (max-width: 1440px) {
  .contacts__link {
    font-size: 25px;
  }
}
.contacts__item--row .contacts__link {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
}
.contacts__working {
  font-size: 16px;
  line-height: 24px;
}
.contacts__callback {
  padding: 0;
  font-size: 16px;
  line-height: 24px;
  color: #D93F3C;
  text-decoration: none;
  border-top: unset;
  border-left: unset;
  border-right: unset;
  border-bottom: 1px solid #D93F3C;
  cursor: pointer;
  background-color: transparent;
  margin-left: 20px;
}
.contacts__list {
  display: flex;
  flex-direction: column;
}
.contacts__item--flex {
  display: flex;
  justify-content: space-between;
}
.contacts__list--flex {
  flex: 1;
  align-items: flex-end;
  margin-right: 33px;
}
.menu-dropdown__link {
  display: flex;
  align-items: center;
  text-transform: uppercase;
  font-size: 16px;
  color: #000;
  text-decoration: none;
}
.menu-dropdown__list {
  display: flex;
  justify-content: space-around;
  max-width: 1440px;
  margin: 0 auto;
}
.menu-dropdown__svg {
  width: 13px;
  height: 8px;
  margin-left: 8px;
}
.menu-dropdown--shadow {
  box-shadow: 0px 3px 4px 0px rgba(0, 0, 0, 0.25);
}
.menu-dropdown__item {
  padding: 16px 0 16px 0;
}
.breadcramps__list {
  display: flex;
  align-items: center;
}
.breadcramps__link {
  font-size: 18px;
  line-height: 24px;
  color: #A4A4A4;
}
.breadcramps__item {
  margin-right: 10px;
}
.general {
  margin-top: 10px;
}
@media (max-width: 1440px) {
  .general__wrapper {
    display: none;
  }
}
.content__box {
  margin: 40px 0 20px 0;
}
@media (max-width: 1440px) {
  .content__box {
    margin: 20px 0 20px 0;
    padding: 0 16px 0 16px;
  }
}
@media (max-width: 1440px) {
  .content__box .heading__h1 {
    font-size: 28px;
  }
}
.content__block {
  display: flex;
  justify-content: flex-end;
  margin-top: 10px;
  padding-right: 16px;
}
@media (min-width: 1440px) {
  .content__block {
    display: none;
  }
}
.content__block .content__button.swiper-button-next,
.content__block .content__button.swiper-button-prev {
  border: 1px solid #6F0000;
  background-color: #FFFFFF;
  position: static;
  width: 49px;
  z-index: unset;
}
.content__block .content__button.swiper-button-next::after,
.content__block .content__button.swiper-button-prev::after {
  font-size: 13px;
  color: #6F0000;
}
.content__block .content__item {
  display: flex;
  align-items: flex-end;
  justify-content: center;
  height: 50px;
}
.content__inner {
  position: relative;
  margin-top: 46px;
}
@media (max-width: 1440px) {
  .content__inner {
    margin: 30px 16px 0 16px;
  }
}
.content__inner::after {
  content: '';
  position: absolute;
  left: 0;
  display: block;
  width: 100%;
  border-left: 4px solid #D93F3C;
  top: 0;
  height: 100%;
}
.content .swiper-pagination {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  position: static;
  padding: 0 16px 0 16px;
}
@media (min-width: 1440px) {
  .content .swiper-pagination {
    display: none;
  }
}
.content .swiper-pagination__progress {
  display: block;
  width: 100%;
  height: 3px;
  border-radius: 3px;
  background-color: #A4A4A4;
  margin-top: 20px;
  position: relative;
}
.content .swiper-pagination__progress::before {
  content: '';
  display: block;
  width: var(--pagination-progress, 0%);
  border-radius: inherit;
  background-color: #D93F3C;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}
.content .swiper-pagination__fraction {
  margin-top: 15px;
}
.heading__h1 {
  font-family: 'Libre Franklin', sans-serif;
  font-weight: 400;
  font-size: 56px;
}
.heading__h2 {
  font-family: 'Libre Franklin', sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 35px;
}
.heading__h3 {
  font-family: 'Libre Franklin', sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 35px;
}
.heading__h4 {
  font-family: 'Libre Franklin', sans-serif;
  font-weight: 400;
  font-size: 30px;
  line-height: 35px;
}
.heading__red {
  color: #D93F3C;
}
.heading__description {
  font-size: 18px;
  margin-top: 53px;
  line-height: 30px;
}
.heading__border {
  border-top: 4px solid #D93F3C;
  width: 55px;
  display: block;
  margin-top: 30px;
}
.values__description {
  font-size: 20px;
  font-weight: 400;
  margin: 0 0 15px 0;
}
@media (max-width: 1440px) {
  .values__description {
    font-size: 18px;
  }
}
.values__list {
  margin-left: 20px;
}
.production {
  position: relative;
}
.production::before {
  display: block;
  content: url('');
  background-image: url("/img/background.png");
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
}
@media (max-width: 1440px) {
  .production .heading__h2 {
    font-size: 32px;
  }
}
@media (max-width: 1440px) {
  .production .heading__br {
    display: none;
  }
}
@media (max-width: 1440px) {
  .production .heading__item:first-child {
    margin-top: 40px;
  }
}
@media (max-width: 1440px) {
  .production .heading__description {
    margin-top: 0;
    font-size: 16px;
  }
}
@media (max-width: 1440px) {
  .production .heading__border {
    margin-top: 15px;
  }
}
.production__list {
  margin-top: 69px;
}
@media (max-width: 1440px) {
  .production__list {
    margin-top: 0;
  }
}
.production__parameters {
  position: relative;
  font-size: 18px;
  padding: 19px 0 5px;
  white-space: nowrap;
}
@media (max-width: 1440px) {
  .production__parameters {
    font-size: 16px;
    white-space: unset;
    padding: 38px 0 5px;
  }
}
.production__parameters::before {
  content: "";
  border-bottom: 1px solid #000;
  width: 464px;
  display: block;
  position: absolute;
  bottom: 0;
}
@media (max-width: 1440px) {
  .production__parameters::before {
    width: 100%;
  }
}
.production__parameters--border::before {
  content: none;
}
.production__red {
  color: #D93F3C;
}
.production__wrapper {
  display: flex;
