.production__poster {
  height: 100%;
}
.production__inner {
  max-width: 517px;
}
@media (max-width: 1440px) {
  .production__inner {
    max-width: unset;
  }
}
@media (max-width: 1440px) {
  .production__image {
    width: 100%;
    height: 100%;
  }
}
